/*Bootstrap Variables*/
/* Media Queries */
/*=== MEDIA QUERY ===*/
/*=== MEDIA QUERY with max width===*/
.one-column-slider.two-row .slick-active > div, .one-column-slider.three-row .slick-active > div {
  position: relative;
}

.one-column-slider.two-row .slick-active > div:before, .one-column-slider.three-row .slick-active > div:before {
  content: "";
  position: absolute;
  background: #ececec;
  left: 25px;
  height: 1px;
  bottom: -1px;
  right: 25px;
}

.normal-slider .slick-slide {
  position: relative;
}

.normal-slider .slick-slide:before {
  content: "";
  width: 1px;
  position: absolute;
  background: #ececec;
  left: 0;
  top: 10px;
  bottom: 5px;
}

/*
====> Reset Styles
*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/*=== MEDIA QUERY ===*/
/*=== MEDIA QUERY with max width===*/
body {
  font-family: 'Rubik', sans-serif;
  font-weight: 400;
  color: #555555;
  font-size: 14px;
  line-height: 1.65;
  width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

a,
span {
  display: inline-block;
}

a {
  transition: 0.4s;
  color: inherit;
}

a:focus {
  outline: none !important;
}

a:hover {
  text-decoration: none;
}

a:visited,
a:active {
  outline: none;
  border: none;
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
}

img {
  max-width: 100%;
}

p {
  line-height: 20px;
}

.btn:focus,
.btn:active {
  box-shadow: none;
  border-color: transparent;
}

button {
  cursor: pointer;
  background: transparent;
  border: none;
}

button:focus {
  outline: none;
}

.icon {
  font-size: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500;
}

.card {
  border: none;
  border-radius: 0;
}

/* Slick Slider Resets */
.slick-slide:focus {
  outline: none;
}

.slick-slide:focus a:focus {
  outline: none;
}

@media (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

.overflow-hidden {
  overflow: hidden;
}

.site-wrapper {
  overflow: hidden;
}

