}

.timeing {
  display: block;
  width: 100%;
  margin-bottom: 20px;
}

.timeing ion-row {
  margin-left: -5px;
  margin-right: -5px;
}

.timeing span {
  display: block;
  text-align: center;
  background: var(--ion-color-primary-contrast);
  border-radius: 30px;
  font-size: 1rem;
  padding: 10px 0;
}

.fixed {
  position: fixed;
  margin: auto;
  left: 0;
  right: 0;
  z-index: 99;
  bottom: 0;
  width: calc(100% - 32px);
  margin-bottom: 10px;
}

p {
  font-size: 1.15rem;
}

.segment {
  overflow: auto;
}

.segment-button {
  min-width: 73px;
}

.calender-container {
  width: 100%;
  overflow: hidden;
  background: var(--ion-color-primary-contrast);
}

.calander {
  width: 100%;
  overflow-x: auto;
  position: relative;
}

.calander .calander-row .red {
  border: 1px dotted var(--ion-color-danger);
  opacity: 0.3;
}

.calander .calander-row .green {
  border: 1px dotted var(--ion-color-success);
}

.calander .calander-row .day {
  width: 50px;
  position: relative;
  font-size: 2.0rem;
  font-weight: 600;
  text-align: center;
  height: 5rem;
  float: left;
}

.calander .calander-row .day span {
  display: block;
  font-size: 1.1rem;
  color: var(--ion-color-medium-shade);
  font-weight: 300;
}

.calander .calander-row .day::after {
  content: '';
  display: block;
  width: 50%;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  bottom: 0;
  height: 2px;
  background: var(--ion-color-primary-contrast);
}

.calander .calander-row .day.active::after {
  background: var(--ion-color-danger);
}

.icon-calander {
  height: calc(5rem + 16px);
  background: var(--ion-color-primary-contrast);
  width: 40px;
  text-align: center;
  line-height: calc(5rem + 16px);
  position: absolute;
  top: 0px;
  right: 0;
}

.icon-calander::after {
  content: '';
  display: block;
  width: 120px;
  position: absolute;
  left: -120px;
  bottom: 0;
  height: 100%;
  background: linear-gradient(to right, rgba(255, 255, 255, 0.3), white);
  z-index: 1;
}

